import * as d3 from "d3";
export default class NewspaperDrawer {
    constructor(
        data, // Êï∞ÊçÆÂÖ®ÈõÜ
        mode="newsCount" // Ê∏≤ÊüìÊ®°Âºè
    ) {
        this.data = data;
        this.mode = mode;

        this.preprocess();
    }

    preprocess() {
        if (this.mode === "newsCount") {
            this.countScale = d3.scaleLinear()
                .domain([0, d3.max(this.data, d => d.newsCount)])
                .range([0, 1]);
        }
        else if (this.mode === "imgCount") {
            this.countScale = d3.scaleLinear()
                .domain([0, d3.max(this.data, d => d.imgCount)])
                .range([0, 1]);
        } else if (this.mode === "inkratio") {
            this.countScale = d3.scaleLinear()
                .domain([0, d3.max(this.data, d => parseFloat(d.inkratio.replace("%", "")) / 100)])
                .range([0, 1]);
        } else if (this.mode === "imgColor") {
        }
    }

    draw(g, width, height) {
        const datum = g.datum();
        switch (this.mode) {
            case "newsCount":
                return this.drawNewsCount(g, width, height, datum);
            case "imgCount":
                return this.drawImgCount(g, width, height, datum);
            case "inkratio":
                return this.drawInkRatio(g, width, height, datum);
            case "imgColor":
                return this.drawImgColor(g, width, height, datum);
            default:
                return null;
        }
    }

    drawNewsCount(g, width, height, datum) {
        const color = d3.interpolateBlues(this.countScale(datum.newsCount));
        const textColor = d3.hsl(color).l > 0.5 ? "black" : "white";
        g.append("rect")
            .attr("width", width)
            .attr("height", height)
            .attr("fill", color);
        // g.append("text")
        //     .attr("x", width / 2)
        //     .attr("y", height / 2)
        //     .attr("text-anchor", "middle")
        //     .attr("alignment-baseline", "middle")
        //     .attr("fill", textColor)
        //     .attr("user-select", "none")
        //     .text(datum.newsCount);
    }
    
    drawImgCount(g, width, height, datum) {
        const color = d3.interpolateBlues(this.countScale(datum.imgCount));
        const textColor = d3.hsl(color).l > 0.5 ? "black" : "white";
        g.append("rect")
            .attr("width", width)
            .attr("height", height)
            .attr("fill", color);
        // g.append("text")
        //     .attr("x", width / 2)
        //     .attr("y", height / 2)
        //     .attr("text-anchor", "middle")
        //     .attr("alignment-baseline", "middle")
        //     .attr("fill", textColor)
        //     .attr("user-select", "none")
        //     .text(datum.imgCount);
    }

    drawInkRatio(g, width, height, datum) {
        const inkRatioString = datum.inkratio
        const inkRatioValue = parseFloat(inkRatioString.replace("%", ""));
        const inkRatioPercentage = inkRatioValue.toFixed(0) + "%";
        console.log(inkRatioPercentage)
        const color = d3.interpolateBlues(this.countScale(inkRatioValue.toFixed(0) / 100))
        const textColor = d3.hsl(color).l > 0.5 ? "black" : "white";
        g.append("rect")
            .attr("width", width)
            .attr("height", height)
            .attr("fill", color);
        // g.append("text")
        //     .attr("x", width / 2)
        //     .attr("y", height / 2)
        //     .attr("text-anchor", "middle")
        //     .attr("alignment-baseline", "middle")
        //     .attr("fill", textColor)
        //     .attr("user-select", "none")
        //     .text(inkRatioPercentage);
    }

    drawImgColor(g, width, height, datum){
        const fillColor = datum.imgColor[0] ? datum.imgColor[0].color : 'white';
        console.log("Color: ", fillColor); // Ë∞ÉËØïËæìÂá∫
        g.append("rect")
            .attr("width", width)
            .attr("height", height)
            .attr("fill", fillColor);
    }

    // drawInkRatio(g, width, height, datum) {
    //     const color = d3.interpolateBlues(this.countScale(datum.inkratio));
    //     const textColor = d3.hsl(color).l > 0.5 ? "black" : "white";
    //     g.append("rect")
    //         .attr("width", width)
    //         .attr("height", height)
    //         .attr("fill", color);
    //     g.append("text")
    //         .attr("x", width / 2)
    //         .attr("y", height / 2)
    //         .attr("text-anchor", "middle")
    //         .attr("alignment-baseline", "middle")
    //         .attr("fill", textColor)
    //         .attr("user-select", "none")
    //         .text(datum.inkratio);
    // }
}