<template>
  <svg class="detail-view">
    <DetailPage 
      :data="currentData"
      @close="goBack"
    />
  </svg>
</template>

<script>
import DetailPage from '@/components/DetailPage.vue'
import store from '@/store/index.js'

export default {
  name: 'DetailView',
  components: {
    DetailPage
  },
  data() {
    return {
      currentData: null
    }
  },
  methods: {
    goBack() {
      this.$router.push('/')
    }
  },
  created() {
    const date = this.$route.params.date
    this.currentData = store.state.data.find(d => 
      d.time.toISOString().split('T')[0] === date
    )
    if (!this.currentData) {
      this.goBack()
    }
  }
}
</script>

<style scoped>
.detail-view {
  width: 100%;
  height: 100%;
  background: white;
}
</style> 