<template>
  <svg class="home">
    <Title
    :transform='`translate(${paddingX}, ${paddingY})`'
      :width="innerWidth"
      :height="titleHeight"
      :globalWidth="width"
      :globalHeight="height"
    ></Title>
    <ModeSelector
      :transform='`translate(${paddingX + innerWidth * 0.6}, ${paddingY})`'
      :width="innerWidth"
      :height="titleHeight"
      :globalWidth="width"
      :globalHeight="height"
    ></ModeSelector>
    <Newspapers
      :transform='`translate(${paddingX}, ${paddingY + titleHeight})`'
      :width="innerWidth"
      :height="newspapersHeight"
      :globalWidth="width"
      :globalHeight="height"
    ></Newspapers>
  </svg>
</template>

<script>
// @ is an alias to /src
import Title from '@/components/Title.vue'
import Newspapers from '@/components/Newspapers.vue'
import ModeSelector from '@/components/ModeSelector.vue'

const getWidth = () => document.getElementById("app").clientWidth;
const getHeight = () => document.getElementById("app").clientHeight;

export default {
  name: 'HomeView',
  components: {
    Title,
    Newspapers,
    ModeSelector,
  },
  data () {
    return {
      width: getWidth(),
      height: getHeight(),
    }
  },
  computed: {
    paddingX() {
      return this.width * 45 / 1080;
    },
    paddingY() {
      return this.height * 54 / 1080;
    },
    titleHeight() {
      return this.height * 80 / 1080;
    },
    newspapersHeight() {
      return this.height - this.paddingY * 2 - this.titleHeight;
    },
    innerWidth() {
      return this.width - this.paddingX * 2;
    },
    innerHeight() {
      return this.height - this.paddingY * 2;
    },
  },
  methods: {
    resize() {
      this.width = getWidth();
      this.height = getHeight();
    },
  },
  mounted() {
    window.addEventListener('resize', this.resize.bind(this));
  },
}
</script>
<style>
.home {
  width: 100%;
  height: 100%;
}
</style>
