import { createStore } from 'vuex'
import * as d3 from 'd3';

const store = createStore({
  state: {
    data: null,
    mode: "newsCount",
  },
  mutations: {
    setData(state, data) {
      state.data = data;
    },
    setMode(state, mode) {
      state.mode = mode;
    }
  },
  actions: {
  },
  modules: {
  }
})


export default store;