<template>
    <g>
        <g id="axis-x"
            :transform="`translate(${axisYWidth}, 0)`"
        ></g>
        <g id="axis-y"
            :transform="`translate(0, ${axisXHeight})`"
        ></g>
        <g id="newspapers"
            :transform="`translate(${axisYWidth}, ${axisXHeight})`"
        ></g>
        <g id="tooltip"></g>
    </g>
</template>
<script>
import { mapMutations, mapState, mapGetters } from 'vuex';
import * as d3 from 'd3';
import Layout from '../utils/layout.js';
import NewspaperDrawer from '../utils/newspaper-drawer.js';

export default {
    name: 'Newspapers',
    components: {
    },
    data() {
        return {
            rows: 0,
            columns: 0,
            squareSize: 0,
            textSize: 0,
            marginX: 0,
            marginY: 0,
            zoomTransform: d3.zoomIdentity,
        }
    },
    props: {
        width: {
            type: Number,
            default: 0,
        },
        height: {
            type: Number,
            default: 0,
        },
        globalWidth: {
            type: Number,
            default: 0,
        },
        globalHeight: {
            type: Number,
            default: 0,
        },
    },
    computed: {
        ...mapState({
            data: state => state.data,
            mode: state => state.mode,
        }),
        size() {
            return {
                width: this.width,
                height: this.height,
            }
        },
        globalX() {
            if (this.$el === undefined) {
                return 0;
            }
            this.size;
            return this.$el.getBoundingClientRect().x;
        },
        globalY() {
            if (this.$el === undefined) {
                return 0;
            }
            this.size;
            return this.$el.getBoundingClientRect().y;
        },
        axisXHeight() {
            return this.height * 0.03;
        },
        axisYWidth() {
            return this.width * 0.02;
        },
        innerWidth() {
            return this.width - this.axisYWidth;
        },
        innerHeight() {
            return this.height - this.axisXHeight;
        },
        layout() {
            return new Layout(
                this.innerWidth,
                this.innerHeight,
                0.1,
                0.1,
                "calendar",
            );
        },
        newspaperDrawer() {
            return new NewspaperDrawer(
                this.data,
                this.mode,
            );
        }
    },
    methods: {
        layout() {
            
        },
        draw() {
            this.drawAxis();
            this.drawNewspapers();
        },
        drawAxis() {
            
            const axisX = d3.select(this.$el)
                .select("#axis-x");

            const axisY = d3.select(this.$el)
                .select("#axis-y");

            const fontSize = Math.min(this.axisXHeight, this.axisYWidth) * 0.3;
            
            axisX.selectAll("*").remove();
            axisY.selectAll("*").remove();

            axisX.append("line")
                .attr("transform", `translate(0, ${this.axisXHeight * 0.9})`)
                .attr("x1", 0)
                .attr("x2", this.innerWidth)
                .attr("stroke", "white")
                .attr("stroke-width", 1);

            axisX.selectAll(".tick")
                .data(this.layout.axisX())
                .join("g")
                .classed("tick", true)
                .attr("transform", d => `translate(${d.x}, ${this.axisXHeight * 0.9})`)
                .each((d, i, elements) => {
                    const g = d3.select(elements[i]);
                    g.append("line")
                        .attr("y2", - this.axisXHeight * 0.15)
                        .attr("stroke", "white")
                        .attr("stroke-width", 1);
                    g.append("text")
                        .text(d.text)
                        .attr("y", - this.axisXHeight * 0.3)
                        .attr("text-anchor", "middle")
                        .attr("font-size", fontSize)
                        .attr("fill", "black");
                });

            axisY.append("line")
                .attr("transform", `translate(${this.axisYWidth * 0.9}, 0)`)
                .attr("y1", 0)
                .attr("y2", this.innerHeight)
                .attr("stroke", "white")
                .attr("stroke-width", 1);

        
            axisY.selectAll(".tick")
                .data(this.layout.axisY())
                .join("g")
                .classed("tick", true)
                .attr("transform", d => `translate(${this.axisYWidth * 0.9}, ${d.y})`)
                .each((d, i, elements) => {
                    const g = d3.select(elements[i]);
                    g.append("line")
                        .attr("x2", -this.axisYWidth * 0.15)
                        .attr("stroke", "white")
                        .attr("stroke-width", 1);
                    g.append("text")
                        .text(d.text)
                        .attr("x", -this.axisYWidth * 0.25)
                        .attr("text-anchor", "end")
                        .attr("alignment-baseline", "middle")
                        .attr("font-size", fontSize)
                        .attr("fill", "black");
                });
        },
        drawNewspapers() {
            d3.select(this.$el)
                .select("#newspapers")
                .selectAll(".newspaper")
                .data(this.data, d => d)
                .join("g")
                .classed("newspaper", true)
                .each((d, i, elements) => {
                    const g = d3.select(elements[i]);
                    const transform = this.getTransform(d.time);
                    g.attr("transform", `translate(${transform.x}, ${transform.y})`);
                    g.selectAll("*").remove();
                    this.drawNewspaper(g, transform.width, transform.height);
                });
        },
        drawNewspaper(g, width, height) {
            // ÁªòÂà∂
            this.newspaperDrawer.draw(g, width, height);

            // ‰∫§‰∫í
            g.style("cursor", "pointer")
                .on("mouseenter", (event, d) => {
                    g.select("rect")
                        .attr("stroke", "red");
                    
                    const x = event.clientX - this.globalX;
                    const y = event.clientY - this.globalY;
                    const width = this.width * 0.15;

                    const container = d3.select(this.$el)
                        .select("#tooltip");
                    container.selectAll("*").remove();
                    const tooltip = container.append("g")
                        .attr("transform", `translate(${x}, ${y})`);

                    // Êó•ÊúüËΩ¨%Y-%m-%dÊ†ºÂºè
                    const date = d.time;
                    const dateStr = `${date.getFullYear()}-${(date.getMonth() + 1).toString().padStart(2, "0")}-${date.getDate().toString().padStart(2, "0")}`;

                    const image = tooltip.append("image")
                        .attr("xlink:href", `http://127.0.0.1:14563/get-image?date=${dateStr}`)
                        .attr("x", 10)
                        .attr("y", 10)
                        .attr("width", width)
                        .attr("preserveAspectRatio", "xMidYMid meet");

                    image.node().onload = () => {
                        const height = image.node().getBBox().height;
                        image
                            .attr("x", x + width > this.innerWidth ? -width - 10 : 10)
                            .attr("y", y + height > this.innerHeight ? - height - 10 : 10);
                    }

                })
                .on("mousemove", (event, d) => {
                    const x = event.clientX - this.globalX;
                    const y = event.clientY - this.globalY;
                    d3.select(this.$el)
                        .select("#tooltip")
                        .select("g")
                        .attr("transform", `translate(${x}, ${y})`);
                })
                .on("mouseleave", (event, d) => {
                    g.select("rect")
                        .attr("stroke", null);
                    d3.select(this.$el)
                        .select("#tooltip")
                        .selectAll("*")
                        .remove();
                })
              .on("click", (event, d) => {
                const date = d.time.toISOString().split('T')[0];
                this.$router.push(`/detail/${date}`);
              });
        },
        getTransform(date) {
            return this.layout.getTransform(date);
        },
        initZoom() {
            const svg = d3.select(this.$el);
            const newspapers = svg.select("#newspapers");
            const zoom = d3.zoom()
                .scaleExtent([0.5, 5]) // ÂèØÊ†πÊçÆÈúÄË¶ÅË∞ÉÊï¥Áº©ÊîæËåÉÂõ¥
                .on("zoom", (event) => {
                    this.zoomTransform = event.transform;
                    newspapers.attr("transform", event.transform);
                });
            svg.call(zoom);
        }
    },
    mounted() {
        this.initZoom();
        console.log("state", this.data);
        this.draw();
    },
    watch: {
        layout() {
            this.draw();
        },
        newspaperDrawer() {
            this.draw();
        }
    }
}
</script>