<template>
    <g id="mode-selector">
        
    </g>
</template>
<script>
import { mapMutations, mapState } from 'vuex';
import * as d3 from 'd3';
export default {
    name: 'Title',
    data() {
        return {
            modes: [
                {
                    name: 'È¢úËâ≤',
                    value: 'imgColor',
                },
                {
                    name: 'ÁâàÈù¢Êï∞Èáè',
                    value: 'newsCount',
                },
                {
                    name: 'ÂõæÁâáÊï∞Èáè',
                    value: 'imgCount',
                },
                {
                    name: 'Â¢®Ê∞¥ÊØî',
                    value: 'inkratio',
                }, 
            ]
        }
    },
    props: {
        width: {
            type: Number,
            default: 0,
        },
        height: {
            type: Number,
            default: 0,
        },
        globalHeight: {
            type: Number,
            default: 0,
        },
        globalWidth: {
            type: Number,
            default: 0,
        },
    },
    computed: {
        ...mapState({
            mode: state => state.mode,
        }),
        size() {
            return {
                width: this.width,
                height: this.height,
            }
        },
        fontsize() {
            return this.globalHeight  * 20 / 1080;
        }
    },
    methods: {
        ...mapMutations([
            'setMode',
        ]),
        draw() {
            const selector = d3.select('#mode-selector');
        let x = 0;
        selector.selectAll('.mode')
            .data(this.modes)
            .join("g")
            .classed('mode', true)
            .each((d, i, elements) => {
                const g = d3.select(elements[i]);
                g.selectAll('*').remove();
                const radius = g.append("g");
                radius.append('circle')
                    .attr('cx', x + this.fontsize * 0.5)
                    .attr('cy', this.height * 0.5)
                    .attr('r', this.fontsize * 0.5)
                    .attr('fill', 'white')
                    .attr('stroke', 'black')
                    .attr('stroke-width', 1);
                if (d.value === this.mode) {
                    radius.append('circle')
                        .attr('cx', x + this.fontsize * 0.5)
                        .attr('cy', this.height * 0.5)
                        .attr('r', this.fontsize * 0.35)
                        .style('fill', 'black');
                }
                x += this.fontsize * 1.2;
                g.append('text')
                    .text(d => d.name)
                    .attr('x', x)
                    .attr('y', this.height * 0.5)
                    .attr('font-size', this.fontsize)
                    .style('fill', 'black')
                    .style("dominant-baseline", "middle")
                x += d.name.length * this.fontsize + this.fontsize;
            })
            .style('cursor', 'pointer')
            .on('click', (e, d) => {
                this.setMode(d.value);
            });
        }
    },
    mounted() {
        this.draw();
    },
    watch: {
        size() {
            this.draw();
        },
        mode() {
            this.draw();
        }
    }
}
</script>