export default class Layout {
    constructor(
        width,
        height,
        paddingX = 0.1,
        paddingY = 0.1,
        mode="canlendar"
    ) {
        this.width = width;
        this.height = height;
        this.ratio = 1/3;
        this.paddingX = paddingX;
        this.paddingY = paddingY;
        this.mode = mode;

        this.preprocess();
    }

    preprocess() {
        if (this.mode === "calendar") {
            let dw = this.width / (366 + 365 * this.paddingX);
            let dh = this.height / (80 + 79 * this.paddingY);
            let dx, dy;
            if (dw / dh > this.ratio) {
                dw = dh * this.ratio;
                dx = dw + (this.width - dw * 366) / 365;
                dy = dh * (1 + this.paddingY);
            }
            else if (dw / dh < this.ratio) {
                dh = dw / this.ratio;
                dx = dw * (1 + this.paddingX);
                dy = dh + (this.height - dh * 80) / 79;
            }
            else {
                dx = dw * (1 + this.paddingX);
                dy = dh * (1 + this.paddingY);
            }
            this.dx = dx;
            this.dy = dy;
            this.dw = dw;
            this.dh = dh;
            console.log(this.dx, this.dy, this.dw, this.dh);
        }
    }

    getTransform(date) {
        switch (this.mode) {
            case "calendar":
                return this.getCanlendarTransform(date);
            default:
                return null;
        }

    }

    getCanlendarTransform(date) {
        const year = date.getFullYear();
        const startDate = new Date(year, 0, 1);
        const dayOfYear = Math.floor((date - startDate) / (1000 * 60 * 60 * 24));
        return {
            x: dayOfYear * this.dx,
            y: (year - 2023) * this.dy,
            width: this.dw,
            height: this.dh
        };
    }

    axisX() {
        switch (this.mode) {
            case "calendar":
                return this.axisXCalendar();
            default:
                return null;
        }
    }

    axisY() {
        switch (this.mode) {
            case "calendar":
                return this.axisYCalendar();
            default:
                return null;
        }
    }

    isLeapYear(year) {
        return (year % 4 === 0 && year % 100 !== 0) || (year % 400 === 0);
    }

    axisXCalendar() {
        let result = [];
        let currentX = 0;
        
        // ÊØè‰∏™ÊúàÊòæÁ§∫1Âè∑
        const daysInMonth = [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
        
        for (let month = 0; month < 12; month++) {
            result.push({
                x: currentX + this.dw / 2,
                width: this.dw,
                text: `${month + 1}Êúà1Âè∑`
            });
            currentX += this.dx * daysInMonth[month];
        }
        
        return result;
    }

    axisYCalendar() {
        return new Array(3).fill(0).map((_, i) => {
            const year = 2023 + i;
            return {
                y: i * this.dy + this.dh / 2,
                height: this.dh,
                text: `${year}Âπ¥`,
            };
        });
    }
}